
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  height: 100vh;
  font-size: 1.6rem;
  font-family: Montserrat, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
}

/* Layout */
.grid-container {
  display: grid;
  grid-template-areas: 'header' 'main' 'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 6rem 1fr 5rem;
  height: 100%;
}
header {
  grid-area: 'header';
  background-color: #fff;
  margin-top: 0.2rem;
  font-size: 1.5rem;
  letter-spacing: 0.1em;
}
main {
  grid-area: main;
  padding: 1rem;
  margin-top: 3rem;
}
footer {
  grid-area: footer;
  background-color: #000;
  color: #b5b5b5;
}

/* Common */
h1 {
  font-size: 1.8rem;
  padding: 1rem 0;
}
h2 {
  font-size: 1.6rem;
  padding: 1rem  0;
}
a {
  text-decoration: none;
}
a:hover { 
  color: #c19d56
}

ul{
  padding: 0;
  margin: 0;
  list-style-type: none;
}

li {
  margin-top: 1rem;
}

button {
  font-family: Montserrat,sans-serif;
  letter-spacing: 0.19rem;
  text-transform: uppercase;
  color: #fff;
  outline: #c19d56 solid 1px;
  border: 1px solid transparent;
  background-color: #c19d56;
  vertical-align: middle;
  outline-offset: 2px;
  cursor: pointer;
  /* transition: color .2s ease-in-out,background-color .2s ease-in-out,border-color .2s ease-in-out,outline-color .2s ease; */
}

button.primary.block {
  padding: 1rem 3.2rem;
}

button:hover {
  color: #c19d56;
  background-color: #fff;
  /* font-size: 1.5rem; */
}

/* Header */
header a {
  color: #000;
  padding: 1rem;
  margin-bottom: 1.3rem;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.row.center {
  justify-content: center;
}

.row.top {
  align-items: flex-start;
}

.col-1{
  flex: 1 1 25rem;
  align-self: center;
}

.col-2{
  flex: 2 1 50rem;
}

/* Images */
img {
  border-radius: 0.5rem;
}
img.medium {
  max-width: 29rem;
  width: 100%;
}
img.large {
  max-width: 35rem;
  width: 100%;
}

.imageMadelineLogo { 
  margin-top: 1.5rem;
  padding-top: 0.2rem;
  max-height: 6.5rem;
  height: 100%;
  position:absolute;
  left:50%;
  transform:translate(-50%, -50%);
}

/* Home */
.homeText {
  margin-left: 10rem;

}

.mainLetter {
  color: #c19d56;   
  font-family: "Great Vibes", sans-serif;
  font-size: 5rem;
  max-width: 60vw;
}

.middleText {
  font-family: Cormorant, serif;
  font-size: 2rem;
  max-width: 60vw;
}

.largeText {
  font-family: Montserrat, serif;
  font-size: 5rem;
  font-weight: 700;
  max-width: 60vw;
}
/* Card */
.card {
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem; 
}

.card-body {
  padding: 1rem;
}
.card-body > * {
  margin-bottom: 0.2rem;
}
.price {
  font-size: 2.1rem;
}

.kategoria { 
  font-family: Cormorant, serif;
  font-style: italic;
}

/* Rating */ 
.rating span{
  color: #f0c040;
  margin: 0.1rem;
}
.rating span:last-child {
  color: #404040
}

/* Alert */
.success {
  color: #20a020;
}
.error {
  color: #a02020;
}